import React, { useState, useEffect } from 'react';
import { supabase } from './supabaseClient';

function App() {
  // ---   (System States) ---
  const [activeTab, setActiveTab] = useState('files'); 
  const [darkMode, setDarkMode] = useState(false);
  const [filesList, setFilesList] = useState([]);
  const [requestsList, setRequestsList] = useState([]);
  const [notification, setNotification] = useState({ show: false, message: '', type: '' });
  
  // ---    ---
  const [file, setFile] = useState(null);
  const [customFileName, setCustomFileName] = useState('');
  const [contributor, setContributor] = useState('');
  const [selectedSubject, setSelectedSubject] = useState('');
  const [uploading, setUploading] = useState(false);
  const [progress, setProgress] = useState(0);
  const [searchQuery, setSearchQuery] = useState('');
  const [filterSubject, setFilterSubject] = useState('');

  // ---   ---
  const [reqName, setReqName] = useState('');
  const [reqSubject, setReqSubject] = useState('');

  // ---   ---
  const [activeFileDetails, setActiveFileDetails] = useState(null); //   
  const [commentText, setCommentText] = useState('');
  const [allComments, setAllComments] = useState([]);

  const subjects = ["", "", "", "", "", "", ""];

  const showMsg = (msg, type = 'success') => {
    setNotification({ show: true, message: msg, type: type });
    setTimeout(() => setNotification({ show: false, message: '', type: '' }), 4000);
  };

  const fetchData = async () => {
    const { data: fData } = await supabase.from('files').select('*').order('created_at', { ascending: false });
    if (fData) setFilesList(fData);
    const { data: rData } = await supabase.from('requests').select('*').order('created_at', { ascending: false });
    if (rData) setRequestsList(rData);
    const { data: cData } = await supabase.from('comments').select('*');
    if (cData) setAllComments(cData);
  };

  useEffect(() => { fetchData(); }, []);

  const handleUpload = async () => {
    if (!file || !customFileName || !selectedSubject || !contributor) return showMsg("   !", "error");
    setUploading(true);
    const storagePath = `${Date.now()}.pdf`;
    const { error: upErr } = await supabase.storage.from('pdfs').upload(storagePath, file, {
        onUploadProgress: (evt) => setProgress(Math.round((evt.loaded / evt.total) * 100))
    });
    if (!upErr) {
      const { data: urlData } = supabase.storage.from('pdfs').getPublicUrl(storagePath);
      await supabase.from('files').insert([{ 
        name: customFileName, url: urlData.publicUrl, 
        subject: selectedSubject, contributor_name: contributor 
      }]);
      showMsg("  !  " + contributor);
      setCustomFileName(''); setFile(null); setProgress(0); fetchData();
    }
    setUploading(false);
  };

  const handleRequest = async () => {
    if (!reqName || !reqSubject) return showMsg("   ", "error");
    const { error } = await supabase.from('requests').insert([{ student_name: reqName, subject_name: reqSubject }]);
    if (!error) {
        showMsg("    ");
        setReqName(''); setReqSubject(''); fetchData();
    }
  };

  const submitComment = async (fileId) => {
    if (!commentText) return showMsg("  ", "error");
    await supabase.from('comments').insert([{ file_id: fileId, comment_text: commentText, user_name: "" }]);
    showMsg("    ");
    setCommentText(''); fetchData();
  };

  const filteredFiles = filesList.filter(item => {
    const matchesSearch = item.name.toLowerCase().includes(searchQuery.toLowerCase());
    const matchesSubject = filterSubject === '' || item.subject === filterSubject;
    return matchesSearch && matchesSubject;
  });

  const getLeaderboard = () => {
    const counts = {};
    filesList.forEach(f => counts[f.contributor_name] = (counts[f.contributor_name] || 0) + 1);
    return Object.entries(counts).sort((a, b) => b[1] - a[1]);
  };

  const theme = {
    bg: darkMode ? '#121212' : '#f8f9fa',
    card: darkMode ? '#1e1e1e' : '#ffffff',
    text: darkMode ? '#ffffff' : '#2c3e50',
    border: darkMode ? '#333' : '#eee'
  };

  return (
    <div style={{ padding: '20px', direction: 'rtl', fontFamily: 'Arial', maxWidth: '600px', margin: 'auto', minHeight: '100vh', backgroundColor: theme.bg, color: theme.text, transition: '0.3s' }}>
      
      {notification.show && <div style={toastStyle(notification.type)}>{notification.message}</div>}

      <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: '20px' }}>
        <button onClick={() => setDarkMode(!darkMode)} style={{ background: 'none', border: 'none', fontSize: '24px', cursor: 'pointer' }}>
            {darkMode ? '' : ''}
        </button>
        <h1 style={{ color: '#2ecc71', margin: 0, fontSize: '22px' }}>  </h1>
      </div>

      <div style={{ ...navStyle, backgroundColor: theme.card }}>
        <button onClick={() => setActiveTab('files')} style={tabBtn(activeTab === 'files')}> </button>
        <button onClick={() => setActiveTab('requests')} style={tabBtn(activeTab === 'requests')}> </button>
        <button onClick={() => setActiveTab('leaderboard')} style={tabBtn(activeTab === 'leaderboard')}> </button>
      </div>

      {activeTab === 'files' && (
        <>
          <div style={{ ...cardStyle, backgroundColor: theme.card, borderColor: theme.border }}>
            <h3 style={{ marginTop: 0 }}>   </h3>
            <input placeholder="" value={contributor} onChange={e => setContributor(e.target.value)} style={{...inputStyle, backgroundColor: theme.bg, color: theme.text}} />
            <input placeholder=" " value={customFileName} onChange={e => setCustomFileName(e.target.value)} style={{...inputStyle, backgroundColor: theme.bg, color: theme.text}} />
            <select value={selectedSubject} onChange={e => setSelectedSubject(e.target.value)} style={{...inputStyle, backgroundColor: theme.bg, color: theme.text}}>
              <option value=""> </option>
              {subjects.map(s => <option key={s} value={s}>{s}</option>)}
            </select>
            <input type="file" accept="application/pdf" onChange={e => setFile(e.target.files[0])} style={{ marginBottom: '10px', fontSize: '12px' }} />
            {uploading && <div style={pBar}><div style={{...pFill, width: `${progress}%`}}></div></div>}
            <button onClick={handleUpload} disabled={uploading} style={buttonStyle}>{uploading ? `  ${progress}%` : ''}</button>
          </div>

          <div style={{ margin: '20px 0' }}>
            <input placeholder="   ..." value={searchQuery} onChange={e => setSearchQuery(e.target.value)} style={{...inputStyle, backgroundColor: theme.card, color: theme.text, border: '1px solid #2ecc71'}} />
            <div style={{ display: 'flex', gap: '5px', flexWrap: 'wrap', marginTop: '10px' }}>
                <button onClick={() => setFilterSubject('')} style={filterBtn(filterSubject === '')}></button>
                {subjects.map(s => <button key={s} onClick={() => setFilterSubject(s)} style={filterBtn(filterSubject === s)}>{s}</button>)}
            </div>
          </div>

          {filteredFiles.map(item => (
            <div key={item.id} style={{ ...itemCard, backgroundColor: theme.card, borderColor: theme.border }}>
              <div style={{ display: 'flex', gap: '10px', alignItems: 'center' }}>
                <div style={pdfIcon}></div>
                <div>
                  <span style={badgeStyle}>{item.subject}</span>
                  <strong style={{ display: 'block', fontSize: '14px' }}>{item.name}</strong>
                  <small style={{ color: '#888', fontSize: '11px' }}>: {item.contributor_name}</small>
                </div>
              </div>
              <div style={{ display: 'flex', flexDirection: 'column', gap: '5px' }}>
                <a href={item.url} target="_blank" rel="noreferrer" style={downloadBtn}></a>
                <button onClick={() => setActiveFileDetails(activeFileDetails === item.id ? null : item.id)} style={infoBtn}> </button>
              </div>

              {activeFileDetails === item.id && (
                <div style={{...commentSection, backgroundColor: theme.bg}}>
                   <h4 style={{marginTop: 0, fontSize: '13px'}}> :</h4>
                   <div style={{ display: 'flex', gap: '5px' }}>
                     <input placeholder="    ..." value={commentText} onChange={e => setCommentText(e.target.value)} style={{...inputStyle, padding: '8px', marginBottom: '5px'}} />
                     <button onClick={() => submitComment(item.id)} style={{...buttonStyle, padding: '0 15px', height: '35px', width: 'auto'}}></button>
                   </div>
                   <div style={{ marginTop: '10px', maxHeight: '150px', overflowY: 'auto' }}>
                     {allComments.filter(c => c.file_id === item.id).length > 0 ? 
                      allComments.filter(c => c.file_id === item.id).map(c => (
                        <div key={c.id} style={{ fontSize: '11px', borderBottom: '1px solid ' + theme.border, padding: '8px 0' }}>
                           <span style={{fontWeight: 'bold', color: '#2ecc71'}}>:</span> {c.comment_text}
                        </div>
                      )) : <p style={{fontSize: '11px', opacity: 0.5}}>   ..</p>
                     }
                   </div>
                </div>
              )}
            </div>
          ))}
        </>
      )}

      {activeTab === 'requests' && (
        <>
          <div style={{ ...cardStyle, backgroundColor: theme.card, borderColor: theme.border }}>
            <h3 style={{ marginTop: 0 }}>    </h3>
            <input placeholder="" value={reqName} onChange={e => setReqName(e.target.value)} style={{...inputStyle, backgroundColor: theme.bg, color: theme.text}} />
            <input placeholder="   " value={reqSubject} onChange={e => setReqSubject(e.target.value)} style={{...inputStyle, backgroundColor: theme.bg, color: theme.text}} />
            <button onClick={handleRequest} style={buttonStyle}> </button>
          </div>
          <h3 style={{ fontSize: '16px' }}> :</h3>
          {requestsList.map(req => (
            <div key={req.id} style={{ ...itemCard, backgroundColor: theme.card, borderColor: theme.border, borderRight: '5px solid #f1c40f' }}>
              <div>
                <strong style={{ fontSize: '14px' }}>: {req.subject_name}</strong>
                <p style={{ fontSize: '12px', color: '#888', margin: '5px 0' }}>: {req.student_name}</p>
              </div>
              <div style={{ fontSize: '20px' }}></div>
            </div>
          ))}
        </>
      )}

      {activeTab === 'leaderboard' && (
        <div style={{ ...cardStyle, backgroundColor: theme.card }}>
          <h2 style={{ textAlign: 'center', color: '#2ecc71' }}>   </h2>
          {getLeaderboard().map(([name, count], index) => (
            <div key={name} style={{ display: 'flex', justifyContent: 'space-between', padding: '15px', borderBottom: '1px solid ' + theme.border }}>
              <span>
                {index === 0 ? ' ' : index === 1 ? ' ' : index === 2 ? ' ' : `${index + 1}. `}
                {name}
              </span>
              <span style={{ fontWeight: 'bold', color: '#2ecc71' }}>{count} </span>
            </div>
          ))}
        </div>
      )}
    </div>
  );
}

// ---   ---
const navStyle = { display: 'flex', justifyContent: 'space-around', padding: '10px', borderRadius: '15px', marginBottom: '20px', boxShadow: '0 2px 10px rgba(0,0,0,0.05)' };
const tabBtn = (active) => ({ padding: '10px 15px', border: 'none', background: active ? '#2ecc71' : 'none', color: active ? '#fff' : '#666', borderRadius: '10px', fontWeight: 'bold', cursor: 'pointer' });
const cardStyle = { padding: '20px', borderRadius: '20px', border: '1px solid #eee', marginBottom: '20px', boxShadow: '0 4px 15px rgba(0,0,0,0.05)' };
const inputStyle = { width: '100%', padding: '12px', marginBottom: '10px', borderRadius: '10px', border: '1px solid #eee', boxSizing: 'border-box', outline: 'none' };
const buttonStyle = { width: '100%', padding: '12px', background: '#2ecc71', color: '#fff', border: 'none', borderRadius: '10px', fontWeight: 'bold', cursor: 'pointer' };
const itemCard = { padding: '15px', borderRadius: '15px', marginBottom: '12px', display: 'flex', justifyContent: 'space-between', alignItems: 'center', border: '1px solid #eee', flexWrap: 'wrap' };
const pdfIcon = { background: '#feeef0', color: '#e74c3c', padding: '10px', borderRadius: '10px', fontSize: '20px' };
const badgeStyle = { background: '#e8f5e9', color: '#2e7d32', padding: '2px 8px', borderRadius: '10px', fontSize: '10px' };
const downloadBtn = { textDecoration: 'none', background: '#3498db', color: '#fff', padding: '5px 12px', borderRadius: '8px', fontSize: '12px', textAlign: 'center' };
const infoBtn = { background: '#95a5a6', color: '#fff', border: 'none', padding: '5px 12px', borderRadius: '8px', fontSize: '12px', cursor: 'pointer' };
const commentSection = { width: '100%', marginTop: '15px', padding: '15px', borderRadius: '10px' };
const filterBtn = (active) => ({ padding: '5px 12px', borderRadius: '20px', border: '1px solid #2ecc71', background: active ? '#2ecc71' : 'none', color: active ? '#fff' : '#2ecc71', cursor: 'pointer', fontSize: '11px' });
const pBar = { background: '#eee', height: '8px', borderRadius: '5px', marginBottom: '10px', overflow: 'hidden' };
const pFill = { background: '#2ecc71', height: '100%', transition: '0.3s' };
const toastStyle = (type) => ({ position: 'fixed', top: '20px', left: '50%', transform: 'translateX(-50%)', background: type === 'success' ? '#2ecc71' : '#e74c3c', color: '#fff', padding: '10px 25px', borderRadius: '50px', zIndex: 1000, fontWeight: 'bold' });

export default App;